import { AfterViewInit, Component, OnInit, ViewChild } from "@angular/core";
import { NavController } from "@ionic/angular";
import { Chart } from "chart.js";

@Component({
  selector: "app-meal-planner",
  templateUrl: "./meal-planner.page.html",
  styleUrls: ["./meal-planner.page.scss"],
})
export class MealPlannerPage implements OnInit, AfterViewInit {
  id;
  selectTabs = "grafico";
  slideOpts1 = {
    slidesPerView: 1.3,
  };
  select;
  exames:any[] = [];
  mes;
  historico;
  perfil;
  grupo = { id: "0" };
  meses = [];
  @ViewChild("mealChart") mealChart;

  bars: any;

  constructor(
    private navctr: NavController,
    private router: Router,
    private examSrv: ExamsService,
    private route: ActivatedRoute,
    private userSrv: UserService
  ) {}

  ngOnInit() {
    this.loadExams();
  }
  ngAfterViewInit() {}

  onClick() {}

  onBack() {
    this.navctr.back();
  }

  goToExams() {
    let navigationExtras: NavigationExtras = {
      queryParams: {
        grupo_id: this.id,
      },
    };
    this.router.navigate(["exams"], navigationExtras);
  }
  backGroupExames() {
    this.navctr.back();
  }

  loadExams() {
    this.route.queryParams.subscribe((data) => {
      this.id = data.grupo_id;
      const dataAtual = new Date();
      this.mes = dataAtual.getMonth() + 1;
      console.log(data);
      this.examSrv.getGroupExam(this.id).subscribe((data) => {
        this.perfil = data[0]["perfil"];
      });
      this.loadHistorico(this.id);
      this.loadUltimosExames(this.id);
    });
  }

  loadHistorico(id) {
    this.grupo.id = id;
    this.userSrv.historicoUserExames(this.grupo).subscribe((data) => {
      this.historico = data;
    });
  }

  loadUltimosExames(id) {
    this.grupo.id = id;
    this.userSrv.utlimosExames(this.grupo).subscribe((data) => {
      this.exames = data;
      this.select = data[0]["mes"];
      console.log(this.exames);
    });
  }
}
